import React from 'react'
import { motion } from 'framer-motion';
import IMG0 from '../../statics/rules0.jpg';
import {
    RulesWrapprr,
    RulesContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper,
    ListItem
} from './style'
const Rules = () => {
    return (
        <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
            <RulesWrapprr>
                <RulesContainer>
                    <TopWrapper>
                        <TitleWrapper><Title>訂房須知</Title></TitleWrapper>
                        <ImgWrapper src={IMG0}/>
                    </TopWrapper>
                    <BottomWrapper>
                    
                    <ListItem className='main'>西螺後街童畫民宿  訂房洽詢時間 AM 08:00 ～ PM 21:00</ListItem>
                    <ListItem className='gray'><span>付款帳號</span>華南銀行 西螺分行 <span>542-20-0057520</span> 陳韋橋</ListItem>
                    <ListItem>訂房確認後,請在<span>3日</span>內匯款房價<span>50%</span>作為訂金,餘款現場支付。</ListItem>
                    <ListItem className='gray'>匯款完成後請以簡訊<span> 0988-096533 </span>告知姓名、住宿日期與帳號後五碼以供確認,若超過保留時間內而未收到訂金者將視為自動放棄。</ListItem>
                    <ListItem>在確認收到您的匯款後我們會回以簡訊，才算「訂房成功」喔。尾款部分於住房時以現金付清。</ListItem>
                    <ListItem className='gray'>民宿進退房時間：<span> AM11:00 </span>前退房<span> PM15:00 </span>後進房</ListItem>
                    <ListItem>依照2009年12月10日公佈之觀光旅館、旅館業、民宿訂房定型化契約範本規定：</ListItem>
                    <ListItem className='gray'>旅客出發前 <span>14</span> 天前解除契約者，民宿退還訂金<span>100%</span>。</ListItem>
                    <ListItem>旅客出發前 <span>10~13</span> 天前解除契約者，民宿退還訂金<span>70%</span>。</ListItem>
                    <ListItem className='gray'>旅客出發前 <span>7~9</span> 天前解除契約者，民宿退還訂金<span>50%</span>。</ListItem>
                    <ListItem>旅客出發前 <span>4~6</span> 天前解除契約者，民宿退還訂金<span>40%</span>。</ListItem>
                    <ListItem className='gray'>旅客出發前 <span>2~3</span> 天前解除契約者，民宿退還訂金<span>30%</span>。</ListItem>
                    <ListItem>旅客出發前 <span>1</span> 天前解除契約者，民宿退還訂金<span>20%</span>。</ListItem>
                    <ListItem className='gray'>旅客出發當天解除契約或未通知解約者民宿一律不退費。</ListItem>
                    <ListItem>退款一律以<span>ATM</span>轉帳方式處理，<span>3天</span>內完成退款手續並以簡訊通知旅客。轉帳時所產生之費用由旅客自行負擔由退款金額中扣除。</ListItem>
                    <ListItem className='gray'>退費計算方式: 例: 出發前三天通知取消訂房者扣訂金<span>1000*30%=300</span>元<span>300</span>(退款部份)-<span>30</span>(匯款手續費)=<span>270</span>(退款金額)</ListItem>
                    <ListItem className='main'>天災不可抗拒之因素：如遇天災如颱風、地震、飛機停飛等不可抗力因素，經雲林縣政府發布之訊息，導致交通中斷無法前往者可扣除轉帳手續費後全額退費。</ListItem>
                    </BottomWrapper>
                </RulesContainer>
            </RulesWrapprr>
        </motion.div>
    )
}

export default Rules
