import React from 'react'
import { v4 } from 'uuid';
import {
    JourneyWrapper,
    JourneyImgWrapper,
    JourneyImg,
    JourneyDetail
} from '../style'
const JourneyItem = ({imgChange, img, title, dsc, extradsc1, extradsc2, wrapperChange}) => {
    return (
        <>
            <JourneyWrapper wrapperChange={wrapperChange}>
                <JourneyImgWrapper>
                    {
                        img.map((item)=>{
                            return <JourneyImg imgChange={imgChange} key={v4()} src={item}/>
                        })
                    }            
                </JourneyImgWrapper>
                <JourneyDetail>
                    <h3>{title}</h3>
                    <ul>
                        {
                          dsc.map((item)=>{
                              return <li key={v4()}>{item}</li>
                          }) 
                        }
                    </ul>  
                    <ul>
                        {
                          extradsc1.map((item)=>{
                              return <li key={v4()}>{item}</li>
                          }) 
                        }
                    </ul>   
                    <ul>
                        {
                          extradsc2.map((item)=>{
                              return <li key={v4()}>{item}</li>
                          }) 
                        }
                    </ul>   
                </JourneyDetail>
            </JourneyWrapper>
        </>
    )
}

export default JourneyItem
