export const HeaderNav = styled.div`
    /* width:65%;  */
    display:flex;
    /* justify-content:flex-end; */
    padding:30px 0;
    @media screen and (max-width:960px){
        display:none;
    }
`;
export const HeaderItem = styled.div`
    text-align:center;
    white-space:nowrap;
    padding:0 50px;
`;
export const ItemLink = styled(LinkR)`
    text-decoration:none;
    color:#fff;
    font-size:15px;
    font-weight:500;
    &.active{
        /* padding:5px 5px; */
        color:#A0A09F;
        /* border-bottom:2px solid #CCCC33;  */
    }
    &:hover{
        color:#A0A09F;
    }
    
`;