import styled from 'styled-components';
export const RoomsWrapprr = styled.div`
    width:100%;
    box-sizing:border-box;
    overflow:hidden;
`;
export const RoomsContainer = styled.div`
    width:67%;
    margin:0 auto;
    @media screen and (max-width:960px){
        width:90%;
    }
`;
export const TopWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    /* height:300px; */
    margin-bottom:30px;
    @media screen and (max-width:960px){
        flex-direction:column;
        /* height:500px; */
        margin-bottom:0;
    }
    @media screen and (max-width:768px){
        /* flex-direction:column; */
        /* height:400px; */
    }
    @media screen and (max-width:480px){
        /* height:265px; */
    }
`;
export const TitleWrapper = styled.div`
    width:25%;
    float:left;
    background-color:#000;
    @media screen and (max-width:960px){
        width:100%;
    }
`;
export const Title = styled.div`
    color:#fff;
    font-size:30px;
    font-weight:bold;
    padding:30px 20px;
    @media screen and (max-width:960px){
        font-size:28px;
    }
`;
export const ImgWrapper = styled.img`
    width:68%;
    object-fit:cover center;
    float:right;
    @media screen and (max-width:960px){
        width:100%
    }
`;
export const BottomWrapper = styled.div`
    margin-bottom:30px;
    width:100%;
    padding:0 23px;
    @media screen and (max-width:960px){
        font-size:14px;
        padding:0 0;
        margin-top:10px;
    }
`;

export const MapImg = styled.img`
    width:100%;
    margin-bottom:10px;
    @media screen and (max-width:960px){
    }
`;
export const CarImgWrapper = styled.div`
    display:flex;
    justify-content:space-between;
    width:100%;
    margin-bottom:10px;
    @media screen and (max-width:960px){
    }
`;
export const CarImgOne = styled.img`
    width:33%;
    @media screen and (max-width:960px){
    }
`;
export const CarText = styled.div`
    width:33%;
    text-align:center;
    background-color: #000;
    display:flex;
    flex-direction:column;
    justify-content:center;
    p{
        margin-bottom:0;
        font-weight:bold;
        line-height:2;
        color:#fff;
    }
    @media screen and (max-width:768px){
        p{
            line-height:1.5;
            font-size: 13px;
        }
    }
    @media screen and (max-width:480px){
        p{
            line-height:1.3;
            font-size: 7px;
        }
    }
`;
export const CarImgTwo = styled.img`
    width:33%;

`;
export const DetailWrapper = styled.div`
    display:flex;
    flex-direction:column;
    background-color: #fff;
    padding:20px 10px;
    color:#000;
    h2{
        text-align:center;
        margin-bottom:10px;
        font-weight:bold;
    }
    @media screen and (max-width:960px){
        font-size:18px;
        h2{
            font-size:27.5px;
        }
    }
    @media screen and (max-width:768px){
        font-size:15px;
        h2{
            font-size:24px;
        }
    }
    @media screen and (max-width:480px){
        font-size:12px;
        h2{
            font-size:20px;
        }
    }
`;

export const DetailItem = styled.div`
    display:flex;
    padding:20px 0;
    justify-content:center;
    align-items:center;
    border-bottom:1px solid #A0A09F;
    &.borderNone{
        border:none;
    }
    @media screen and (max-width:960px){
        flex-direction:column;
    }
`;

export const DataItemTitle = styled.div`
    width:35%;
    text-align:center;
    font-weight:bold;

    @media screen and (max-width:960px){
    }
`;

export const ItemData = styled.div`
    width:63%;
    @media screen and (max-width:960px){
        width:100%;
        margin-top:10px;
    }
`;

export const DataItem = styled.div`
    padding:5px;
    line-height:2;
    &.special{
        font-weight:bold;
    }
    &.mainTitle{
        background-color:#D8D8D8;
    }
    &.roadDetail{
        
    }
    /* @media screen and (max-width:480px){
        font-size:10px;
    } */
`;