import { useState, useEffect } from 'react';
import Header from './common/header';
import SideBar from './common/sidebar';
import ScrollToTop from './common/scrolltotop';
import Footer from './common/footer';
import Home from './pages/home';
import Rules from './pages/rules';
import Roomintroduction from './pages/roomintroduction';
import Journey from './pages/journey';
import HowToPlay from './pages/howtoplay';
import Transportation from './pages/transportation';
import { Route, Switch, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';
import './App.css';
const App = () => {
  const [isOpen, setOpen] =  useState(false);

  useEffect(()=>{
    if(isOpen){
      document.body.style.overflow = 'hidden';
    }else{
      document.body.style.overflow = 'unset';
    }
  },[isOpen])

  let location = useLocation();

  const handleSidebar =() =>{
    setOpen(!isOpen)
}
  return (
    <>
      <SideBar isOpen={isOpen} handleSidebar={handleSidebar}/>
      <ScrollToTop />
      <Header isOpen={isOpen} handleSidebar={handleSidebar}/>
      <AnimatePresence exitBeforeEnter>
        <Switch location={location} key={location.pathname}>
          <Route path='/' exact component={Home} />
          <Route path='/rules' component={Rules} />
          <Route path='/roomintroduction/:id' component={Roomintroduction} />
          <Route path='/journey' component={Journey} />
          <Route path='/howtoplay' component={HowToPlay} />
          <Route path='/transportation' component={Transportation} />
        </Switch>
      </AnimatePresence>
      <Footer/>
    </>
  );
}

export default App;
