import React, { useState, useEffect } from 'react'
import { motion } from 'framer-motion';
import { IoIosClose } from 'react-icons/io';
import RoomDetailImgSection from './roomDetailImgSection';
import { v4 } from 'uuid';
import {
    HomestayRoomWrapper,
    RoomDetailTop0,
    BackBtn0,
    RoomDetailName0,
    RoomWrapper,
    RoomImg,
    RoomTitle,
    RoomData,
    RoomText,
    RoomBtn,
    DetailBtn,
    RoomDetailWrapper,
    RoomDetailRight,
    RoomDetailLeft,
    RoomDetailTop,
    BackBtn,
    RoomDetailName,
    RoomDetailDataWrapper,
    DRoomWrapper,
    RoomPrice,
    RoomDetailData,
    ExtraImgWrapper,
    ExtraImg,
    ShowExtraImgWrapper,
    ShowExtraImg
} from '../style'
const HomeStay = ({list, title}) => {
    const [showDetail, setShowDetail] = useState(0)
    const [showImg, setShowImg] = useState(false)
    const [imgState, setImgState] = useState('')
    useEffect(()=>{
        window.scrollTo({
            top:0, 
            left:0,
            behavior:'instant',
          })
      },[showDetail])
    const handleContent = (id) =>{
        setShowDetail(id)
    }
    const handleBackContent = () =>{
        setShowDetail(0)
    }
    const handleImgShow = (item) =>{
        setShowImg(!showImg)
        setImgState(item)
    }
    if(showDetail === 0) {
    return (
        <>
        <HomestayRoomWrapper>
            <RoomDetailTop0>
                <BackBtn0 to='/roomintroduction/default'>←</BackBtn0>
                <RoomDetailName0>{title}</RoomDetailName0>
            </RoomDetailTop0>
            {             
                list.map((item)=>{
                    return(
                        <RoomWrapper key={item.id}>
                            <RoomImg src={item.mainImg} onClick={() =>{handleContent(item.id)}}/>
                            <RoomTitle>{item.name}</RoomTitle>
                            <RoomData>
                            <RoomText>
                                <h2>{item.name}</h2>
                                <ul>
                                    {item.facility.map((i)=>{
                                        return <li key={v4()}>{i}</li>
                                    })}
                                </ul>
                            </RoomText>
                            </RoomData>
                            <RoomBtn>
                                <DetailBtn onClick={() =>{handleContent(item.id)}}>查看</DetailBtn>
                            </RoomBtn>
                        </RoomWrapper>
                    )
                })
            }  
        </HomestayRoomWrapper>  
        </>
    )}else{
        const newList = list[showDetail-1]
           return (
            <>
            <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
            <HomestayRoomWrapper>
                <RoomDetailWrapper>
                        <RoomDetailTop>
                            <BackBtn onClick={handleBackContent}>←</BackBtn>
                            <RoomDetailName>{newList.name}</RoomDetailName>
                        </RoomDetailTop>
                        <RoomDetailImgSection imglist={newList.detailImg}/>
                        <RoomDetailDataWrapper>
                            <DRoomWrapper>
                                <RoomDetailRight>
                                    <h5>計價方式</h5>                            
                                    <RoomPrice>
                                        <ul>
                                            {
                                                newList.pricelist.map((item)=>{
                                                    return  <li key={v4()}>{item}</li>
                                                })
                                            }
                                            <li>*農曆春節期間價格另計</li>
                                        </ul>
                                    </RoomPrice>
                                </RoomDetailRight>
                                <RoomDetailLeft>
                                    <h5>房間設施</h5>  
                                    <RoomDetailData>
                                        <ul>
                                            {
                                                newList.facility.map((item)=>{
                                                    return  <li key={v4()}>{item}</li>
                                                })
                                            }                                    
                                        </ul>
                                    </RoomDetailData>
                                </RoomDetailLeft>
                            </DRoomWrapper>
                            <ExtraImgWrapper>
                                    {
                                        newList.detailImg2.map((item)=>{
                                            return  <ExtraImg onClick={ ()=>{handleImgShow(item)}} key={v4()} src={item}/>
                                        })                                   
                                    } 
                            </ExtraImgWrapper>
                        </RoomDetailDataWrapper>
                </RoomDetailWrapper>           
                {
                    showImg 
                    ? 
                    <ShowExtraImgWrapper onClick={()=>{setShowImg(!showImg)}}>
                        <IoIosClose className='close'/>
                        <motion.div initial={{ opacity:0, scale:0}} animate={{ opacity:1,scale:1}} exit={{ opacity:0,scale:0}} transition={{ease:"easeOut",duration:.5}}>
                            <ShowExtraImg src={imgState}/> 
                        </motion.div>
                    </ShowExtraImgWrapper>
                    : 
                    null
                }           
            </HomestayRoomWrapper>  
            </motion.div>
            </>
            )
        } 
        
}

export default HomeStay
