import React from 'react'
import { motion } from 'framer-motion';
import IMG0 from '../../statics/rules0.jpg';
import Map from '../../statics/map.jpg';
import CarImg1 from '../../statics/carimg1.jpg';
import CarImg2 from '../../statics/carimg2.jpg';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper,
    MapImg,
    CarImgWrapper,
    CarImgOne,
    CarText,
    CarImgTwo,
    DetailWrapper,
    DetailItem,
    DataItemTitle,
    ItemData,
    DataItem,
} from './style'
const Transportation = () => {
    return (
        <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
        <RoomsWrapprr>
            <RoomsContainer>
                <TopWrapper>
                    <TitleWrapper><Title>交通資訊</Title></TitleWrapper>
                    <ImgWrapper src={IMG0}/>
                </TopWrapper>
                <BottomWrapper>
                    <MapImg src={Map} />
                    <CarImgWrapper>
                        <CarImgOne src={CarImg1}/>
                        <CarText>
                            <p>老街本館</p>
                            <p>設有專屬停車場</p>
                            <p>請住房貴賓</p>
                            <p>以倒車入庫方式</p>
                            <p>靠牆停靠</p>
                        </CarText>
                        <CarImgTwo src={CarImg2}/>
                    </CarImgWrapper>                       
                    <DetailWrapper>
                        <h2>交通資訊</h2>
                        <DetailItem>
                            <DataItemTitle >自行開車建議</DataItemTitle>
                            <ItemData>
                                <DataItem className='special'>西螺後街童畫民宿  連絡電話:0988-096533</DataItem>
                                <DataItem className='mainTitle'>老街本館: (西螺鎮市後街1-2號)</DataItem>
                                <DataItem className='roadDetail'>國道一號→西螺交流道下→往西螺方向→大同路直走→至 市後街 左轉</DataItem>
                                <DataItem className='mainTitle'>福興館: (西螺鎮修文路8７巷１７號) </DataItem>
                                <DataItem className='roadDetail'>國道一號→西螺交流道下→往西螺方向→大同路直走→至 延平路或修文路 左轉</DataItem>
                                <DataItem className='mainTitle'>背包客棧: 請直接與訂房人員聯絡</DataItem>
                            </ItemData>
                        </DetailItem>
                        <DetailItem>
                        <DataItemTitle >客運建議</DataItemTitle>
                            <ItemData>
                                <DataItem className='mainTitle'>國光客運</DataItem>
                                <DataItem className='roadDetail'>路線【台北-西螺-高雄】自6:00~21:00每整點台北(公路西站)、高雄交互發車。</DataItem>
                                <DataItem className='roadDetail'>地址：西螺鎮中山路70號 電話：05-5862055</DataItem>
                                <DataItem className='mainTitle'>台西客運</DataItem>
                                <DataItem className='roadDetail'>路線【西螺-台西】【西螺-麥寮】【西螺-廣興-虎尾】</DataItem>
                                <DataItem className='roadDetail'>【西螺-下湳-虎尾】【西螺-二崙-虎尾】【西螺-饒平-斗六】</DataItem>
                                <DataItem className='roadDetail'>【西螺-埤源-斗六】【西螺-林內】【西螺-二崙-北港】</DataItem>
                                <DataItem className='mainTitle'>員林客運</DataItem>
                                <DataItem className='roadDetail'>路線【西螺-彰化】【西螺-台中】【西螺-嘉義】</DataItem>
                                <DataItem className='mainTitle'>統聯客運</DataItem>
                                <DataItem className='roadDetail'>路線【下崙-北港-台北】【三條崙-西螺-台北】</DataItem>
                                <DataItem className='roadDetail'>地址：西螺鎮振興里116-6號 電話：05-5873624</DataItem>
                                <DataItem className='mainTitle'>日統客運</DataItem>
                                <DataItem className='roadDetail'>路線【梅山-西螺-台北】【三條崙(麥寮)-西螺-台北】</DataItem>
                                <DataItem className='roadDetail'>【麥寮-西螺】地址：西螺鎮振興里116-6號 電話：05-5873703</DataItem>
                            </ItemData>
                        </DetailItem>
                        <DetailItem>
                            <DataItemTitle >火車</DataItemTitle>
                            <ItemData>
                                <DataItem className='roadDetail'>需到斗六火車站轉乘台西客運，路線【下崙-北港-台北】【三條崙-西螺-台北】</DataItem>
                            </ItemData>
                        </DetailItem>
                        <DetailItem className='borderNone'>
                            <DataItemTitle >高鐵</DataItemTitle>
                            <ItemData>
                                <DataItem className='roadDetail'>須到虎尾高鐵站轉乘台西客運，或搭計程車至西螺市區約20分鐘。</DataItem>
                            </ItemData>
                        </DetailItem>
                    </DetailWrapper>
                </BottomWrapper>
            </RoomsContainer>
        </RoomsWrapprr>
        </motion.div>
    )
}

export default Transportation
