import styled from 'styled-components';
import { Link as LinkR } from 'react-router-dom';
export const RoomsWrapprr = styled.div`
    width:100%;
    box-sizing:border-box;
    overflow:hidden;
`;
export const RoomsContainer = styled.div`
    width:67%;
    margin:0 auto;
    @media screen and (max-width:960px){
        width:90%;
    }
`;
export const TopWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    /* height:300px; */
    margin-bottom:30px;
    @media screen and (max-width:960px){
        flex-direction:column;
        /* height:500px; */
        margin-bottom:0;
    }
    @media screen and (max-width:768px){
        flex-direction:column;
        /* height:400px; */
    }
    @media screen and (max-width:480px){
        /* height:265px; */
    }
`;
export const TitleWrapper = styled.div`
    width:25%;
    float:left;
    background-color:#000;
    @media screen and (max-width:960px){
        width:100%;
    }
`;
export const Title = styled.div`
    color:#fff;
    font-size:30px;
    font-weight:bold;
    padding:30px 20px;
    @media screen and (max-width:960px){
        width:100%;
        font-size:28px;
    }
`;
export const ImgWrapper = styled.img`
    width:68%;
    object-fit:cover center;
    float:right;
    @media screen and (max-width:960px){
        width:100%
    }
`;
export const BottomWrapper = styled.div`
    margin-bottom:30px;
    width:100%;
    padding:0 23px;
    margin-top:10px;
    @media screen and (max-width:960px){
        font-size:14px;
        padding:0 0;
    }
`;
export const HomestayIntroWrapper = styled.div`
    width:100%;
    padding:10px 10px;
    display:flex;
    justify-content:space-evenly;
    /* height:400px; */
    background-color: #fff;
    box-sizing:border-box;
    margin-bottom:20px;
    @media screen and (max-width:960px){
        flex-direction:column;
        height:auto;
    }
`;
export const HomestayImg = styled.img`
    width:60%;
    object-fit:cover center;
    height:400px;
    @media screen and (max-width:960px){
        width:100%;
        height:250px;
    }
`;
export const HomestayData = styled.div`
    width:38%;
    padding:10px 10px;
    background-color: #fff;
    /* display:grid; */
    /* grid-template-rows:15% 68% 15%; */
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-evenly;
    h2{
        font-size:25px;
        font-weight:bold;
        color:#000;
        text-align:center;
    }
    p{
        line-height:1.9;
        padding:15px 0;
    }
    @media screen and (max-width:960px){
        width:100%;
        p{
            font-size:15px;
        }
    }
`;
export const ReadMoreBtn = styled(LinkR)`
    width:100%;
    background-color:#000;
    font-size: 20px;
    color:#fff;
    text-align:center;
    padding:15px 0;
    font-weight:bold;
    cursor: pointer;
    transition:0.2s all ease-in;
    text-decoration:none;
    border:1px solid #000;
    display:${({btnchange})=>(btnchange ? 'none' : "block" )};
    :hover{
        background-color: #fff;
        color:#000;
        transition:0.2s all ease-in;
    }
    @media screen and (max-width:960px){
        margin-top:10px;
    }
    
`;
// homeStay
export const HomestayRoomWrapper = styled.div`
    margin-bottom:30px;
    width:100%;
    padding:0 23px;
    color:#fff;

    @media screen and (max-width:960px){
        padding:0 0;

    }
`;
export const RoomDetailTop0 = styled.div`
    display:flex;
    align-items:center;
    color:#000;
    margin-bottom:5px;
`;
export const BackBtn0 = styled(LinkR)`
    text-decoration:none;
    font-size:40px;
    font-weight:bold;
    padding:10px 20px;
    text-align:center;
    margin-right:10px;
    color:#000;
    &:hover{
        color:black;
    }
`;
export const RoomDetailName0 = styled.div`
    font-size:35px;
    font-weight:bold;
    color:#000;
    @media screen and (max-width:960px){
    }
`;
export const RoomWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    height:230px;
    background-color: #fff;
    box-sizing:border-box;
    margin-bottom:20px;
    background:#993333;
    @media screen and (max-width:960px){
        margin-bottom:3px;
        height:250px;
        position:relative;
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    }
`;
export const RoomImg = styled.img`
    width:40%;
    object-fit:cover center;
    cursor: pointer;
    @media screen and (max-width:960px){
        width:100%;
    }
`;
export const RoomTitle = styled.div`
    position:absolute;
    bottom:5px;
    left:10px;
    font-size:30px;
    font-weight:bold;
    display:none;
    @media screen and (max-width:960px){
        display:block;
    }
`;

export const RoomData = styled.div`
    width:60%;
    padding:20px;
    @media screen and (max-width:960px){
        display:none;
    }
`;

export const RoomText = styled.div`
    width:80%;
    display:flex;
    flex-direction:column;
    h2{
        font-size:20px;
        font-weight:bold;
        color:#fff;
    }
    ul{
        padding:12px 15px;
    }
    li{
        font-size:13px;
        line-height:1.3;
        list-style-type: circle;
    }
`;
export const RoomBtn = styled.div`
    width:20%;
    display:flex;
    justify-content:center;
    align-items:center;
    @media screen and (max-width:960px){
        display:none;
    }
`;

export const DetailBtn = styled.div`
    width:60%;
    text-align:center;
    border:1px solid #fff;
    padding:30px 0;
    display:block;
    color:#fff;
    cursor: pointer;
    &:hover{
        color:#fff;
    }
`;
// RoomDetail
export const RoomDetailWrapper = styled.div`
    width:100%;
    background:#993333;
    padding-top:5px;
    @media screen and (max-width:960px){
        font-size:14px;
        padding:0 0;
    }
`;
export const RoomDetailTop = styled.div`
    display:flex;
    align-items:center;
    padding:8px 0;
    @media screen and (max-width:960px){
    }
`;
export const BackBtn = styled.div`
    cursor: pointer;
    font-size:35px;
    font-weight:bold;
    padding:0 20px;
    text-align:center;
    margin-right:10px;
    border-right:2px solid #fff;
    @media screen and (max-width:960px){
    }
`;
export const RoomDetailName = styled.div`
    font-size:30px;
    font-weight:bold;
    @media screen and (max-width:960px){
        font-size:28px;
    }
    @media screen and (max-width:768px){
        font-size:20px;
    }
`;
export const RoomDetailDataWrapper = styled.div`
    display:flex;
    padding:20px;
    width:100%;
    flex-direction:column;
    @media screen and (max-width:960px){

    }
`;
export const DRoomWrapper = styled.div`
    display:flex;
    width:100%;
    @media screen and (max-width:960px){
        flex-direction:column;
    }
`;

export const RoomDetailRight = styled.div`
    width:50%;
    display:flex;
    justify-content:center;
    align-items:center;
    line-height:2;
    padding:20px 0;
    h5{
        width:50%;
        line-height:110px;
        font-weight:bold;
        white-space: nowrap;
        text-align:center;
    }
    @media screen and (max-width:960px){
        width:100%; 
        height:45%;
        border-bottom:1.5px solid #fff;
    }
    @media screen and (max-width:480px){
        h5{
            font-size:15px;
        }
    }
`;
export const RoomDetailLeft = styled.div`
    width:50%;
    display:flex;
    justify-content:center;
    align-items:center;
    line-height:2;
    padding:20px 0;
    h5{
        width:20%;
        line-height:110px;
        font-weight:bold;
        white-space: nowrap;
    }
    @media screen and (max-width:960px){
        width:100%; 
        height:53%;
        padding-top:32px;
        h5{
            width:50%;
            text-align:center;
        }
    }
    @media screen and (max-width:480px){
        h5{
            font-size:15px;
        }
    }
`;
export const RoomPrice = styled.div`
    width:48%;
    font-size:15px;
    @media screen and (max-width:480px){
        li{
            font-size:11px;
        }
    }
`;
export const RoomDetailData = styled.div`
    width:80%; 
    font-size:15px;
    @media screen and (max-width:960px){
        width:48%;
    }
    @media screen and (max-width:480px){
        li{
            font-size:11px;
        }
    }
`;
export const ExtraImgWrapper = styled.div`
    padding-top:20px;
    border-top:1px solid #fff;
    width:100%; 
    display:flex;
`;
export const ExtraImg = styled.img`
    width:20%; 
    margin-right:10px;
    cursor: pointer;
    @media screen and (max-width:960px){
    }
`;
export const ShowExtraImgWrapper = styled.div`
    position: fixed;
    width:100%;
    height:100%;
    background: #000000e1;
    z-index:999;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    top:0;
    left:0;
    .close{
        position:fixed;
        top:0;right:0;
        font-size:70px;
        font-weight:900;
        cursor:pointer;
    }
`;
export const ShowExtraImg = styled.img`
    height:600px;
    @media screen and (max-width:768px){
        height:500px;
    }
    @media screen and (max-width:480px){
        height:400px;
    }
`;
