import React from 'react'
import IMG1 from '../../statics/line.jpg';
import IMG2 from '../../statics/fb.jpg';
import IMG3 from '../../statics/paper.jpg';
import IMG4 from '../../statics/taiwan.jpg';
import {
    FooterWrapper,
    FooterContainer,
    FooterDataWrapper,
    FooterData,
    FooterImgWrapper,
    ImgItem,
    ImgItemA,
    PaperWrapper,
    FooterRight
} from './style';
const Footer = () => {
    return (
        <>
            <FooterWrapper>
                <FooterContainer>
                    <FooterDataWrapper>
                        <FooterData>地址 : 雲林縣西螺鎮延平老街附近巷弄(市後街1-2號)</FooterData>
                        <FooterData>訂房專線 : <span>0988-096533</span></FooterData>
                    </FooterDataWrapper>
                    <FooterImgWrapper>
                        <ImgItem className='contact' src={IMG1}/>   
                        <ImgItemA href={'https://www.facebook.com/%E8%A5%BF%E8%9E%BA%E5%BE%8C%E8%A1%97%E7%AB%A5%E7%95%AB%E6%B0%91%E5%AE%BF%E9%9B%B2%E6%9E%97%E5%8C%85%E6%A3%9F%E5%8C%85%E5%B1%A4-823783791051593/'}><ImgItem  src={IMG2}/></ImgItemA>
                        <PaperWrapper>
                            <ImgItem className='paper' src={IMG3}/>
                            <ImgItem className='paper' src={IMG4}/>
                        </PaperWrapper>
                    </FooterImgWrapper>
                </FooterContainer>
                <FooterRight>
                    <p>© {new Date().getFullYear()} 西螺後街童畫民宿 版權所有 翻譯必究</p> 
                </FooterRight>
            </FooterWrapper>
        </>
    )
}

export default Footer
