import React from 'react'
import IMG0 from '../../statics/rules0.jpg';
import { motion } from 'framer-motion';
import JourneyItem from './component/journeyItem'
import {
    JourneyObj1,
    JourneyObj2,
    JourneyObj3
}  from './component/journeyData';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper
} from './style'
const Journey = () => {
    return (
        <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
        <RoomsWrapprr>
            <RoomsContainer>
                <TopWrapper>
                    <TitleWrapper><Title>套裝行程</Title></TitleWrapper>
                    <ImgWrapper src={IMG0}/>
                </TopWrapper>
                <BottomWrapper>
                    <JourneyItem {...JourneyObj1} />
                    <JourneyItem {...JourneyObj2} />
                    <JourneyItem {...JourneyObj3} />
                </BottomWrapper>
            </RoomsContainer>
        </RoomsWrapprr>
    </motion.div>
    )
}

export default Journey
