import Img from '../../../statics/h1r5_0.jpg';
import styled from 'styled-components';
export const ImgItem = styled.div`
    min-height: 100vh;
    width: 85%;
    overflow:hidden;
    margin:0 auto; 
    background:
    linear-gradient(
      rgba(0, 0, 0, 0.6), 
      rgba(0, 0, 0, 0.6)
    ),
    url(${Img})
    no-repeat
    ;
    background-size: cover;
    background-attachment: fixed;
    background-position: center center;
    position:relative;
@media screen and (max-width:1200px){
    width: 100%;
    min-height: 60vh;
} 
@media screen and (max-width:468px){
    width: 100%;
    background-size: cover;
} 
`;
export const MainTitle = styled.div`
    position:absolute;
    top: 50%; right: 50%;
    transform: translate(50%,-50%);
    text-align:center;
    line-height:2;
    color:#fff;
    .main{
        font-size:7rem;
        font-weight:500;
        white-space:nowrap;
    }
    .second{
        font-size:1rem;
    }
    button{
        outline:none;
        border:none;
        color:#fff;
        background:#A52A2A;
        padding:1rem 3rem;
        margin-top:3rem;
        font-size:1.2rem;
        border-radius:10px;
    }
    button:hover{
        background:#800000;
        transition:.3s ease-in;
    }
    @media screen and (max-width:468px){
        .second{
            white-space:normal
    }
        
} 
`;