import styled from 'styled-components';

export const FooterWrapper = styled.div`
    width:100%;
    overflow:hidden;
    box-sizing:border-box;
    background-color:#000;
    position:relative;
    display:flex;
    justify-content:center;
    margin-top:20px;
`;
export const FooterContainer = styled.div`
    width:80%;
    display:flex;
    justify-content:space-evenly;
    padding:40px 0;
    align-items:center;
    background-color:#000;
    margin:0 auto;
    border-top:1px solid #000;
    color:#fff;
    margin-bottom:50px;
    @media screen and (max-width:960px){
        width: 90%;
        flex-direction:column;
    }
`;
export const FooterDataWrapper = styled.div`
    width:50%;
    @media screen and (max-width:960px){
        width: 100%;
        text-align:center;
        margin-bottom:10px;
    }
`;
export const FooterData = styled.div`
    font-size:14px;
    line-height:50px;
    span{
        font-weight:700;
    }
    @media screen and (max-width:768px){
        font-size:12px;
    }
    /* @media screen and (max-width:480px){
        font-size:8px;
    } */
`;
export const FooterImgWrapper = styled.div`
    width:60%;
    display:flex;
    justify-content:space-around;
    align-items:center;
    @media screen and (max-width:960px){
        width: 100%; 
    }
`;
export const ImgItemA= styled.a`
    width:17%;
    height:120px;
    @media screen and (max-width:760px){
        height:80px;
    }
`;

export const ImgItem = styled.img`
    width:100%;
    height:120px;
    &.contact{
        width:17%;
        height:120px;
    }

    &.paper{
        width:40%;
        height:auto;
    }
    @media screen and (max-width:760px){
        height:80px;
        &.contact{
        height:80px;       
        }
        &.paper{
        height:90px;
    }
    }
`;
export const PaperWrapper= styled.div`
    width:50%;
    display:flex;
    justify-content:space-evenly;
    height:100px;
`;
export const FooterRight= styled.div`
    position:absolute;
    bottom:10px;
    color:#fff;
    font-size:14px;
    text-align:center;
    line-height:1;

    @media screen and (max-width:960px){
        font-size:13.5px;
    }
    @media screen and (max-width:760px){
        font-size:12px;
    }
    @media screen and (max-width:480px){
        line-height:1.2;
    }
`;

