import React from 'react';
import { IoIosClose } from 'react-icons/io';
import { useLocation } from "react-router-dom";
import {
    SideBarWrapper,
    OpacityBlock,
    SideBarMenu,
    // MobIcon,
    SideBarItem,
    SideBarLink
} from './style'

const SideBar = ({ isOpen, handleSidebar}) =>{
    const location = useLocation();
    const { pathname } = location;
    const splitLocation = pathname.split("/");

    return (
        <SideBarWrapper isOpen={isOpen}>
            {/* <OpacityBlock isOpen={isOpen}>
            </OpacityBlock> */}
            <SideBarMenu>
                {/* <MobIcon isOpen={isOpen}>
                    <IoIosClose onClick={handleSidebar} />
                </MobIcon> */}
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === '' ? 'active' : '' } to='/' onClick={handleSidebar}>民宿主頁</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'rules' ? 'active' : '' } to='/rules' onClick={handleSidebar}>認識我們</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'roomintroduction' ? 'active' : '' } to='/roomintroduction/default' onClick={handleSidebar}>房型介紹</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'journey' ? 'active' : '' } to='/journey' onClick={handleSidebar}>套裝行程</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'howtoplay' ? 'active' : '' } to='/howtoplay' onClick={handleSidebar}>西螺好吃好玩</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'transportation' ? 'active' : '' } to='/transportation' onClick={handleSidebar}>交通資訊</SideBarLink>
                </SideBarItem>
            </SideBarMenu>
        </SideBarWrapper>
    )
    }   

export default SideBar
