import styled from 'styled-components';

// export const MainWrapper = styled.div`

//     @media screen and (max-width:960px){
//         width:90%;
//     }
// `;

export const HomeWrapper = styled.div`
    width:100%;
    box-sizing:border-box;
    overflow:hidden;
    margin-top:50px;
    @media screen and (max-width:960px){
        margin-top:10px;
    }
`;
export const HomeContainer = styled.div`
    width:57%;
    margin:0 auto;
    @media screen and (max-width:960px){
        width:90%;
    }
`;
export const TopWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    /* height:300px; */
    @media screen and (max-width:960px){
        flex-direction:column;
        /* height:400px; */
    }
`;
export const TopItemImg = styled.img`
    width:30%;
    object-fit:'cover center';
    @media screen and (max-width:960px){
        display:none;
    }
`;
export const TopTitle = styled.div`
    width:23%;
    background:#000;
    color:white;
    font-size:35px;
    font-weight:800;
    padding:30px 20px;
    box-sizing:border-box;
    @media screen and (max-width:960px){
        width:100%;
        font-size:28px;
    }
`;

export const TopItem = styled.div`
    width:40%;
    border:1px solid #000;
    background-color: #fff;
    text-align:center;
    font-size:14px;
    line-height:2;
    padding:50px 15px;
    @media screen and (max-width:960px){
        width:100%;
        margin-bottom:20px 0;
    }
`;
export const BottomWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    padding-top:20px;
`;
export const LeftContainer = styled.div`
    width:47%;
    display:flex;
    flex-direction:column;
`;
export const TextWithImgWrapper = styled.div`
    position:relative;
    margin-bottom:20px;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
`;
export const LefttItem = styled.img`
    width:100%;
`;
export const TextForImg = styled.div`
    color:white;
    position:absolute;
    width:100%;
    background:#000;
    height:100%;
    opacity:0%;
    transition:0.2s opacity ease-in-out;
    line-height:3;
    text-align:center;
    letter-spacing:4px;
    font-size:14px;
    padding:10px 10px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    &:hover{
        opacity:80%;
        transition:0.2s opacity ease-in-out;
    }
    @media screen and (max-width:960px){
        font-size:15px;
        line-height:2.5;
        /* padding:5px 5px; */
    }
    @media screen and (max-width:480px){
        line-height:1.5;
        font-size:11px;
    }
    @media screen and (max-width:361px){
        line-height:1.2;
        font-size:7px;
    }
`;
export const RightContainer = styled.div`
    width:47.5%;
    display:flex;
    flex-direction:column;
`;
export const RightItem = styled.img`
     width:100%;
`;