import React from 'react';
import { useLocation } from "react-router-dom";
import { IoReorderThreeOutline } from 'react-icons/io5';

import {
    HeaderWrapper,
    HeaderContainer,
    MobIcon,
    // HeaderIcon,
    // IconLink,
    HeaderNav,
    HeaderItem,
    ItemLink
} from './style';
const Header = ({ isOpen, handleSidebar }) => {
    const location = useLocation();
    const { pathname } = location;
    const splitLocation = pathname.split("/");
    return (
        <>
            <HeaderWrapper>
                <HeaderContainer>
                    {/* <HeaderIcon >
                        <IconLink />
                    </HeaderIcon> */}
                    <MobIcon isOpen={isOpen} onClick={handleSidebar}>
                        {/* {
                            isOpen ? <IoIosClose onClick={handleCloseSidebar} /> : <IoReorderThreeOutline onClick={handleOpenSidebar} />
                        } */}
                        {/* <IoReorderThreeOutline  onClick={handleSidebar} /> */}
                        <span class='bar'></span>
                        <span class='bar'></span>
                        <span class='bar'></span>
                    </MobIcon>
                    <HeaderNav>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === '' ? 'active' : '' } to='/'>民宿簡介</ItemLink>
                        </HeaderItem>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === 'rules' ? 'active' : '' } to='/rules' >訂房須知</ItemLink>
                        </HeaderItem>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === 'roomintroduction' ? 'active' : '' } to='/roomintroduction/default'>房型介紹</ItemLink>
                        </HeaderItem>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === 'journey' ? 'active' : '' } to='/journey'>套裝行程</ItemLink>
                        </HeaderItem>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === 'howtoplay' ? 'active' : '' } to='/howtoplay'>西螺好吃好玩</ItemLink>
                        </HeaderItem>
                        <HeaderItem>
                            <ItemLink className={ splitLocation[1] === 'transportation' ? 'active' : '' } to='/transportation'>交通資訊</ItemLink>
                        </HeaderItem>
                    </HeaderNav>
                </HeaderContainer>
            </HeaderWrapper>
        </>
    )
}

export default Header
