import React from 'react'
import { motion } from 'framer-motion';
import IMG0 from '../../statics/rules0.jpg';
import PlayList from './component/playlist';
import { playListObj } from './component/playListData';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper
} from './style'
const HowToPlay = () => {
    return (
        <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
        <RoomsWrapprr>
            <RoomsContainer>
                <TopWrapper>
                    <TitleWrapper><Title>西螺好吃好玩</Title></TitleWrapper>
                    <ImgWrapper src={IMG0}/>
                </TopWrapper>
                <BottomWrapper>
                    <PlayList {...playListObj}/>
                </BottomWrapper>
            </RoomsContainer>
        </RoomsWrapprr>
        </motion.div>
    )
}

export default HowToPlay
