import React from 'react'
import { v4 } from 'uuid';
import {
    PlayListWrapper,
    PlayListTitle,
    PlayListDetail
} from '../style';
const Playlist = ({list}) => {
    return (
        <>
            <PlayListWrapper>
                <PlayListTitle>
                    {/* <h3>【西螺好吃好玩】</h3> */}
                    {/* <h3>轉載各大部落客之文章,感謝對西螺的介紹</h3> */}
                </PlayListTitle>
                <PlayListDetail>
                    <ul>
                        {
                            list.map((item)=>{
                                return <a key={v4()} href={item.url}><li>{item.text}</li></a>
                            })
                        }
                    </ul>
                </PlayListDetail>
            </PlayListWrapper>
        </>
    )
}

export default Playlist
