import styled from 'styled-components';
// import { Link as LinkR } from 'react-router-dom';
export const RoomsWrapprr = styled.div`
    width:100%;
    box-sizing:border-box;
    overflow:hidden;
`;
export const RoomsContainer = styled.div`
    width:67%;
    margin:0 auto;
    @media screen and (max-width:960px){
        width:90%;
    }
`;
export const TopWrapper = styled.div`
    width:100%;
    display:flex;
    justify-content:space-evenly;
    /* height:300px; */
    margin-bottom:30px;
    @media screen and (max-width:960px){
        flex-direction:column;
        /* height:500px; */
        margin-bottom:0;
    }
    @media screen and (max-width:768px){
        flex-direction:column;
        /* height:400px; */
    }
    @media screen and (max-width:480px){
        /* height:265px; */
    }
`;
export const TitleWrapper = styled.div`
    width:25%;
    float:left;
    background-color:#000;
    @media screen and (max-width:960px){
        width:100%;
        font-size:28px;
    }
`;
export const Title = styled.div`
    color:#fff;
    font-size:30px;
    font-weight:bold;
    padding:30px 20px;
    @media screen and (max-width:960px){
        font-size:28px;
    }
`;
export const ImgWrapper = styled.img`
    width:68%;
    object-fit:cover center;
    float:right;
    @media screen and (max-width:960px){
        width:100%
    }
`;
export const BottomWrapper = styled.div`
    margin-bottom:30px;
    width:100%;
    padding:0 23px;
    @media screen and (max-width:960px){
        font-size:14px;
        padding:0 0;
    }
`;
// --baseSection--

export const JourneyWrapper = styled.div`
    margin-top:15px;
    width:100%;
    display:flex;
    background-color:#993333;
    color:#fff;
    margin-bottom:20px;
    @media screen and (max-width:960px){
        flex-direction:column;
        /* height:${({wrapperChange}) => (wrapperChange ? 'auto' :'300px')}; */
    }
    @media screen and (max-width:768px){
        /* height:${({wrapperChange}) => (wrapperChange ? 'auto' :'230px')}; */
    }
`;
export const JourneyImgWrapper = styled.div`
    width:50%;
    display:flex;
    justify-content:space-around;
    align-items:center;
    @media screen and (max-width:960px){
        width:100%;
        height:50%;
    }
`;
export const JourneyImg = styled.img`
    width:100%;
    height:369px;
    width:${({imgChange}) => (imgChange ? '33.5%' :'100%')};
    @media screen and (max-width:960px){
        height:auto;
    }
    /* @media screen and (max-width:760px){
        height:230px;
    } */
`;
export const JourneyDetail = styled.div`
    width:50%;
    padding:30px;
    text-align:center;
    line-height:2.5;
    display:block;
    h3{
        font-weight:bold;
        padding:20px 0;
    }
    ul{
        padding-top:20px;
        padding-left:0;
    }
    @media screen and (max-width:960px){
        width:100%;
        height:50%;
        li{
            font-size:16px;
        }
    }
    @media screen and (max-width:768px){
        font-size:13px;
        h3{
            padding:5px 0;
        }
        li{
            font-size:13px;
        }
    } 
`;