import React from 'react'
import { motion } from 'framer-motion';
import {
    HomestayIntroWrapper,
    HomestayImg,
    HomestayData,
    ReadMoreBtn,
} from '../style'
const HomestaySection = ({ id, img, title, dsc, btnchange}) => {
    return (
        <motion.div initial={{ opacity:0 }} animate={{ opacity:1}} exit={{ opacity:0}} transition={{ease:"easeOut",duration:.5}}>
        <HomestayIntroWrapper>
            <HomestayImg src={img} />
            <HomestayData>
                <h2>{title}</h2>
                <p>{dsc}</p>
                <ReadMoreBtn btnchange={btnchange ? 1: 0} to={'/roomintroduction/'+id}>查看詳情</ReadMoreBtn>
            </HomestayData>
        </HomestayIntroWrapper>
        </motion.div>
    )
}

export default HomestaySection
