import React from 'react'
import { useHistory  } from 'react-router-dom';
import { motion } from 'framer-motion';
import { textShowOne,transitionOne } from '../../../common/animation';
import {
    ImgItem,
    MainTitle
} from './mainsectionstyle'
const Mainsection = () => {
    let history = useHistory()
    const handleToRoomPage = () =>{
        history.push('/roomintroduction/default')
    }
    return (
        <>           
            <ImgItem>
                <MainTitle>
                    <motion.div initial='out' animate='in' exit='out' variants={textShowOne} transition={transitionOne}>
                        <div class='main'>後街童話民宿</div>
                        <div class='second'>後街童畫民宿是一處充滿手繪童趣風格的地方,從事兒童藝術教育十幾年經驗的韋橋老師利用自己所學,親自動手彩繪這棟民宿,在老房子新裝潢的同時又保留懷舊元素,讓復古與現代產生對話</div>
                        <button onClick={handleToRoomPage}>看房間</button>
                    </motion.div>
                </MainTitle>
            </ImgItem>
        </>
    )
}

export default Mainsection
